import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import {MatStepperModule} from '@angular/material/stepper';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import {HttpClientModule, HttpClient} from '@angular/common/http';
import {TranslateModule, TranslateLoader} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';

import { HeaderComponent } from './components/header/header.component';
import { StepperComponent } from './components/stepper/stepper.component';
import { StepperPage } from './components/stepper/stepper.page';
import { LoaderComponent } from './components/loader/loader.component';
import { LoaderSpinComponent } from './components/loader/loader.spin.component';


import { MainComponent } from './pages/main/main.component';
import { SettingsComponent } from './pages/settings/settings.component';
import { AppPageNotFound } from './pages/pagenotfound/app.pagenotfound';
import { RegistrationPage } from "./pages/registration/registration.page";


import {LanguageService} from "./service/language.service";
import {UserSettingsService} from "./service/user.settings.service";
import {LoaderService} from "./service/loader.service";

const appRoutes: Routes = [
  { path: 'settings', component: SettingsComponent },
  { path: 'registration', component: RegistrationPage },
  { path: '',
    redirectTo: '/registration',
    pathMatch: 'full'
  },
  // {
  //   path: 'heroes',
  //   component: HeroListComponent,
  //   data: { title: 'Heroes List' }
  // },
  // { path: '',
  //   redirectTo: '/heroes',
  //   pathMatch: 'full'
  // },
  { path: '**', component: AppPageNotFound }
];

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

@NgModule({
  declarations: [
    MainComponent, HeaderComponent, StepperComponent, StepperPage, SettingsComponent, AppPageNotFound, RegistrationPage,
    LoaderComponent, LoaderSpinComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: false } // <-- debugging purposes only
    ),
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    BrowserAnimationsModule,
    MatStepperModule
  ],
  providers: [LoaderService, LanguageService, UserSettingsService],
  bootstrap: [MainComponent]
})
export class AppModule { }
