export class Utils {
  static extend(...args: any[]): any {
    const newObj = args[0];
    if (args.length > 1) {
      for (let i = 1; i < args.length; i++) {
        let obj = args[i];
        for (const key in obj) {
          //copy all the fields
          newObj[key] = obj[key];
        }
      }
    }
    return newObj;
  };
}
