import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class LoaderService {

  private loaderSignals: number = 0;
  private is_loading:boolean = false;

  constructor() {
    console.log("INIT LoaderService SERVICE");
  }

  isLoading() {
    return this.is_loading;
  }

  showLoader() {
    this.loaderSignals++;
    if (!this.is_loading) {
      this.is_loading = true;
    }
  }

  hideLoader() {
    if (this.loaderSignals > 0) {
      this.loaderSignals--;
    }
    if (this.loaderSignals <= 0) {
      this.is_loading = false;
    }
  }
}
