import {Component, ElementRef, ViewChild} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import moment  from 'moment-timezone';
import {ActivatedRoute} from "@angular/router";
import {LanguageService} from "../../service/language.service";
import {Language} from "../../service/model/language";
import {LoaderService} from "../../service/loader.service";

declare var $;
declare var jquery;

@Component({
  templateUrl: './registration.page.html',
  styleUrls: ['./registration.page.css']
})
export class RegistrationPage {

  private translateService:TranslateService;
  private languageService: LanguageService;

  private languageList:Language[];

  private selectedCode:string;
  private selectedTZ:string;
  private agree:boolean = false;
  private email:string;


  saveBtnDisabled = false;
  isSaving: boolean = false;

  constructor(translateService: TranslateService, languageService: LanguageService,
              private loaderService: LoaderService) {
    this.translateService = translateService;
    this.languageService = languageService;

    this.languageService.getUserLanguage().then((lang) => {
      this.selectedCode = lang;
    });


    this.loadLanguageList();
  }

  ngAfterViewInit() {
    this.selectedTZ = moment.tz.guess();
    // try {
      $("#timezone").timezonePicker({
        quickLink: [],
        selectClass: "form-control",
        filterBoxClass: "form-group",
        hoverTextEl: $("#seltz"),
        defaultValue: this.selectedTZ,
        hoverText: (e, zone) => {
          $("#seltz").text(zone.timezone);
          // this.selectedTZ = zone;
        }
      });
    // } catch (e) {
    // }
  }

  loadLanguageList() {
    const self = this;
    this.languageService.getLanguageList().then(function(languageList) {
      self.languageList = languageList;
    })
  }

  setLanguage(code) {
    this.languageService.setLanguage(code);
    this.selectedCode = code;

    //TEMP
    this.loaderService.showLoader();
    setTimeout(() => {
      this.loaderService.hideLoader();
    }, 1500);
  }

  save() {
    this.isSaving = true;
    this.saveBtnDisabled = true;
    console.log("Save");
  }
}
