import { Injectable } from '@angular/core';
import {Language} from "./model/language";
import {TranslateService} from "@ngx-translate/core";
import {UserSettingsService} from "./user.settings.service";

@Injectable({
  providedIn: 'root',
})
export class LanguageService {

  DEFAULT_LANG = 'en_GB';

  private languageList = [
    new Language("en_GB", "English", "https://finansinvest.autochartist.com/aclite/icons/en_GB.png"),
    new Language("ja_JP", "", "https://finansinvest.autochartist.com/aclite/icons/ja_JP.png"),
    new Language("zh_CN", "", "https://finansinvest.autochartist.com/aclite/icons/zh_CN.png"),
    new Language("zh_TW", "", "https://finansinvest.autochartist.com/aclite/icons/zh_TW.png"),
    new Language("ru_RU", "", "https://finansinvest.autochartist.com/aclite/icons/ru_RU.png"),
    new Language("it_IT", "Italiano", "https://finansinvest.autochartist.com/aclite/icons/it_IT.png"),
    new Language("sv_SE", "Svenska", "https://finansinvest.autochartist.com/aclite/icons/sv_SE.png"),
    new Language("de_DE", "Deutsch", "https://finansinvest.autochartist.com/aclite/icons/de_DE.png"),
    new Language("fr_LU", "Franais", "https://finansinvest.autochartist.com/aclite/icons/fr_LU.png"),
    new Language("es_ES", "Espaol", "https://finansinvest.autochartist.com/aclite/icons/es_ES.png"),
    new Language("pt_PT", "Portugus", "https://finansinvest.autochartist.com/aclite/icons/pt_PT.png"),
    new Language("ko_KR", "", "https://finansinvest.autochartist.com/aclite/icons/ko_KR.png"),
    new Language("ar_AE", "", "https://finansinvest.autochartist.com/aclite/icons/ar_AE.png"),
    new Language("nl_NL", "Nederlandse", "https://finansinvest.autochartist.com/aclite/icons/nl_NL.png"),
    new Language("el_GR", "", "https://finansinvest.autochartist.com/aclite/icons/el_GR.png"),
    new Language("he_IL", "", "https://finansinvest.autochartist.com/aclite/icons/el_GR.png"),
    new Language("hu_HU", "Magyarorszg", "https://finansinvest.autochartist.com/aclite/icons/hu_HU.png"),
    new Language("tr_TR", "Trke", "https://finansinvest.autochartist.com/aclite/icons/tr_TR.png"),
    new Language("pl_PL", "Polski", "https://finansinvest.autochartist.com/aclite/icons/pl_PL.png"),
    new Language("in_ID", "Indonesia", "https://finansinvest.autochartist.com/aclite/icons/in_ID.png"),
    new Language("th_TH", "", "https://finansinvest.autochartist.com/aclite/icons/th_TH.png"),
    new Language("vi_VN", "Ting Vit", "https://finansinvest.autochartist.com/aclite/icons/vi_VN.png"),
    new Language("ro_RO", "Romn", "https://finansinvest.autochartist.com/aclite/icons/ro_RO.png")
  ];

  constructor(private translateService: TranslateService, private userSettingService: UserSettingsService) {
    console.log("INIT LANGUAGE SERVICE");
    this.translateService.setDefaultLang(this.DEFAULT_LANG);
    this.getUserLanguage().then((lang) => {
      this.translateService.use(lang);
    })
  }

  getUserLanguage() {
    return this.userSettingService.getUserSettings().then((settings) => {
      return settings.lang || this.DEFAULT_LANG;
    })
  }

  getLanguageList() {
    return Promise.resolve(this.languageList);
  }

  setLanguage(langCode) {
    this.translateService.use(langCode);
    this.userSettingService.getUserSettings().then((settings) => {
      settings.lang = langCode;
      this.userSettingService.updateSettings(settings)
    })
  }
}
