import {
  AfterContentInit,
  Component,
  ContentChildren} from '@angular/core';
import { PlatformLocation } from '@angular/common'
import {StepperPage} from "./stepper.page";

@Component({
  selector: 'app-stepper',
  templateUrl: './stepper.component.html',
  styleUrls: ['./stepper.component.css']
})
export class StepperComponent implements AfterContentInit {
  @ContentChildren(StepperPage) steps: StepperPage[];

  private state = {
    activeStep: 1
  };

  activeStep = 1;
  private maxStep = 1;

  constructor(private location: PlatformLocation) {
    // location.onPopState((e) => {
    //   console.log('pressed back!', e);
    //   if (e.state && e.state.activeStep) {
    //     this.activeStep = e.state.activeStep;
    //   }
    // });
  }

  ngAfterContentInit(): void {
  }

  nextPage() {
    this.activeStep++;
    this.state.activeStep++;
    this.saveState();
    if (this.maxStep < this.activeStep) {
      this.maxStep = this.activeStep;
    }
  }

  prevPage() {
    if (this.activeStep > 1) {
      this.activeStep--;
    }
  }

  saveState() {
    // console.log("State save", this.state);
    // this.location.pushState(this.state, "Step " + this.activeStep, ""+this.activeStep);
  }

  selectPage(pagenum) {
    if (pagenum < this.maxStep) {
      this.activeStep = pagenum;
    }
  }

}
